<template>
  <a class="link" :href="href" @click="handleClick"><slot></slot></a>
</template>

<script>
export default {
  name: 'vui-link',
  props: {
    to: String
  },
  computed: {
    href() {
      // tovue-routerclickrouter
      if (this.to && !this.added && this.$router) {
        const resolved = this.$router.match(this.to)
        if (!resolved.matched.length) return this.to

        this.$nextTick(() => {
          const listeners = this.$vnode.componentOptions.listeners || {}
          this.added = true

          // click
          if (!listeners.click) {
            this.$el.addEventListener('click', this.handleClick)
          }
        })
        return resolved.path
      }

      return this.to
    }
  },
  methods: {
    handleClick($evt) {
      const listeners = this.$vnode.componentOptions.listeners || {}
      let result
      $evt.preventDefault()

      if (listeners.click) {
        result = listeners.click.call(this, $evt)
      }
      // added=trueclickfalse
      if (this.added && result !== false) {
        this.$router.push(this.href)
      }
    }
  }
}
</script>



