import Vue from 'vue'
import Indicator from './indicator'
import createChainedFunction from '@/utils/createChainedFunction'

const defaultIndicatorType = 'warning'
let pool = []

function createInstance() {
  return new Vue({
    el: document.createElement('div'),
    components: {
      Indicator
    },
    data: {
      visible: false,
      type: defaultIndicatorType, // Indicatorloading, success, warning, failure
      message: null, // 
      className: null, // Toastclass
      onHidden: null // 
    },
    methods: {
      onHiddenChanged() {
        // Toastthis.visible
        this.visible = false
        // DOM
        this.$el.parentNode.removeChild(this.$el)
        // pool
        pool.push(this)
      }
    },
    render(h) {
      const indicatorProps = {
        props: {
          show: this.visible,
          type: this.type
        },
        class: this.className,
        on: {
          hide: createChainedFunction(this.onHiddenChanged, this.onHidden)
        }
      }

      return (
        <indicator {...indicatorProps}>{this.message}</indicator>
      )
    }
  })
}

function getInstance() {
  return pool.length > 0 ? pool.pop() : createInstance()
}

/**
 * IndicatorService
 * Indicatorservice
 * @param {String|Object} options
 *   Stringtype;
 *   ObjectIndicatortype, onHidden
 *     type,
 *     message,
 *     className,
 *     onHidden
 * @param {String} type     Indicatorloading, success, failure, warning
 * @param {Function} onHidden Indicator
 * @example
 * 1. Vue.$indicator('', function() {}) // typewarning
 * 2. Vue.$indicator('', Vue.$indicator.types.LOADING, function() {})
 * 3. Vue.$indicator({type: Vue.$indicator.types.LOADING, message: ''}, function() {})
 */
function IndicatorService(options = {}, type = defaultIndicatorType, onHidden) {
  if (typeof options === 'string') {
    options = {
      message: options
    }
  }

  // typefunctiononHiddenoptionstypewarning
  if (typeof type === 'function') {
    // optionsObjecttype
    options.type = options.type || defaultIndicatorType
    // options.onHidden
    onHidden = type
  } else if (typeof type === 'string') {
    options.type = type
  }

  if (typeof onHidden === 'function') {
    options.onHidden = onHidden
  }

  let instance = getInstance()

  // pool
  instance.type = options.type
  instance.className = options.className
  instance.message = options.message
  instance.onHidden = options.onHidden

  document.body.appendChild(instance.$el)

  Vue.nextTick(function() {
    instance.visible = true
  })
}

/**
 * Indicator4
 */
IndicatorService.types = {
  LOADING: 'loading',
  SUCCESS: 'success',
  FAILURE: 'failure',
  WARNING: 'warning'
}

export default IndicatorService



