import Vue from 'vue'
import Toast from './toast'
import createChainedFunction from '@/utils/createChainedFunction'

let pool = []

function createInstance() {
  return new Vue({
    el: document.createElement('div'),
    components: {
      Toast
    },
    data: {
      visible: false,
      message: null, // 
      className: null, // Toastclass
      iconClass: null, // class'icon icon-home'
      onHidden: null // 
    },
    methods: {
      onHiddenChanged() {
        // Toastthis.visible
        this.visible = false
        // DOM
        this.$el.parentNode.removeChild(this.$el)
        // pool
        pool.push(this)
      }
    },
    render(h) {
      const toastProps = {
        props: {
          show: this.visible
        },
        class: this.className,
        on: {
          hide: createChainedFunction(this.onHiddenChanged, this.onHidden)
        }
      }

      return (
        <toast {...toastProps}>
          {this.iconClass ? (<i class={this.iconClass}></i>) : null}{this.message}
        </toast>
      )
    }
  })
}

function getInstance() {
  return pool.length > 0 ? pool.pop() : createInstance()
}

/**
 * ToastService
 * Toastservice
 * @param {String|Object} options
 *   String;
 *   ObjectToastonHidden
 *     message,
 *     className,
 *     iconClass,
 *     onHidden
 * @param {Function} onHidden Toast
 * @example
 * 1. Vue.$toast('toast', function(){})
 * 2. Vue.$toast({message: 'toast'}, function(){})
 */
function ToastService(options = {}, onHidden) {
  if (typeof options === 'string') {
    options = {
      message: options
    }
  }

  if (typeof onHidden === 'function') {
    options.onHidden = onHidden
  }

  let instance = getInstance()

  // pool
  instance.className = options.className
  instance.iconClass = options.iconClass
  instance.message = options.message
  instance.onHidden = options.onHidden

  document.body.appendChild(instance.$el)

  Vue.nextTick(function() {
    instance.visible = true
  })
}

export default ToastService



